@import 'assets/icon/g6_editor_font.css';

.editor {
  position: relative;
  width: 100%;
  height: calc(100% - 20px)!important;
  // 顶部工具栏
  .top-container {
    position: absolute;
    padding: 4px 16px;
    width: 100%;
    border: 1px solid #e9e9e9;
    height: 42px;
    display: flex;
    // justify-content: space-between;
    align-items: center;
    background: #ffffff;
    box-shadow: 0px 8px 12px 0px rgba(0, 52, 107, 0.04);
    // 工具栏
    .toolbar {
      height: 100%;
      // 命令按钮
      .command {
        &:hover {
          border: 1px solid #d9d9d9;
        }
        color: #1890ff;
        border: 1px solid transparent;
        width: 32px;
        height: 32px;
        margin: 0px 6px;
        border-radius: 2px;
        padding-left: 6px;
        display: inline-block;
        // border: 1px solid rgba(2, 2, 2, 0);
      }
      .disable {
        color: rgba(0, 0, 0, 0.25);
      }
    }
    // 工具栏中靠右的部分
    .data-opt {
      // width: 20%;
      height: 100%;
      text-align: right;
      padding: 8px 0px;
      position: absolute;
      right: 16px;
    }
  }
  // 底部区域（左项目面板、中心画布、右属性面板）
  .bottom-container {
    padding-top: 41px;
    width: 100%;
    height: 100%;
    display: flex;
    background: #f7f9fb;
    justify-content: space-around;
    // .title {
    //   display: flex;
    //   width: 100%;
    //   align-items: center;
    //   height: 34px;
    //   font-weight: 600;
    //   font-size: 16px;
    //   padding: 0 20px;
    //   background: #cfd4d9;
    // }
    // 左边面板
    .left-pannel {
      width: 200px;
      min-width: 200px;
      height: 100%;
      padding: 0px;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      // 项目面板
      .itempannel-container {
        width: 199px;
        height: 192px;
        // 项目中的图
        img {
          width: 64px;
          height: 64px;
          padding: 4px;
          margin-left: 4px;
          margin-right: 4px;
          border-radius: 2px;
          border: 1px solid rgba(0, 0, 0, 0);
          vertical-align: middle;
          cursor: pointer;
        }
      }
    }
    // 中间画布
    .center-pannel {
      background: #ffffff;
      display: inline-block;
      width: 75%;
      height: 100%;
      border: 1px solid #dedbe2;
      // 流程图
      .flow {
        width: 100%;
        height: 100%;
        // height: -webkit-fill-available;
        overflow: hidden;
      }
      // 右键菜单
      .contextmenu {
        margin: 0px;
        padding: 4px 0px;
        width: 120px;
        text-align: left;
        background: white;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
        border-radius: 4px;
        background-clip: padding-box;
        color: #000;
        font-size: 12px;
        font-variant: tabular-nums;
        line-height: 1.5;
        display: none;
        // 命令
        .command {
          height: 22px;
          padding: 4px 12px;
          margin: 0;
          clear: both;
          font-size: 14px;
          font-weight: normal;
          color: rgba(0, 0, 0, 0.65);
          white-space: nowrap;
          cursor: pointer;
          transition: all 0.3s;
          line-height: 22px;
          box-sizing: content-box;
        }
        span {
          margin-left: 8px;
        }
        // 命令悬停
        .command:hover {
          cursor: pointer;
          background: #e6f7ff;
        }
        // 命令禁用
        .disable {
          color: rgba(0, 0, 0, 0.25);
        }
      }
    }
    // 右边面板
    .right-pannel {
      width: 240px;
      min-width: 240px;
      height: 100%;
      display: inline-flex;
      flex-direction: column;
      // 画布详情
      .detail-pannel {
        width: 100%;
        height: calc(100% - 328px);
        min-height: 114px;
        background: #f7f9fb;
        border-left: 1px solid #e6e9ed;
        font-size: 13px;
        .pannel {
          height: 100%;
          overflow-y: hidden;
        }
        // 标题
        .pannel-title {
          height: 48px;
          border-top: 1px solid #dce3e8;
          border-bottom: 1px solid #dce3e8;
          background: #ebeef2;
          color: #000;
          line-height: 48px;
          padding-left: 12px;
        }
        // 内容区域
        .block-container {
          padding: 16px 8px;
          .p {
            margin-bottom: 12px;
          }
          .p.name {
            .el-input {
              width: 60%;
              padding: 0 10px;
            }
          }
          .p.size {
            .el-input {
              width: 30%;
              padding: 0 10px;
            }
          }
          .p.color {
            .el-color-picker {
              vertical-align: middle;
            }
          }
        }
      }
      // 导航器
      .navigator {
        width: 100%;
        height: 288px;
        z-index: 99;
        // 标题
        .pannel-title {
          display: block;
          height: 48px;
          border-top: 1px solid #dce3e8;
          border-bottom: 1px solid #dce3e8;
          background: #ebeef2;
          color: #000;
          line-height: 48px;
          padding-left: 12px;
        }
        // 缩略图
        .mini-map {
          width: 100%;
          height: 240px;
        }
      }
      // 缩放条
      .zoom-slider {
        width: 100%;
        height: 40px;
        padding: 4px 10px;
        background: #ffffff;
      }
    }
  }
}

.ant-collapse {
  /deep/ .ant-collapse-header {
    // height: 32px;
    border-top: 1px solid #dce3e8;
    border-bottom: 1px solid #dce3e8;
    background: #ebeef2;
    color: #000;
    // line-height: 32px;
    // padding-left: 12px;
  }
}


